.chat {
  height: calc(100vh - 130px);
  overflow: hidden;
  outline: none;
  overflow-y: scroll;
  padding: 20px;
  background-color: #ffffff; }

.convo {
  height: calc(100vh - 250px); }

.chat-users,
.chat-body {
  padding-left: 0; }

.chat-users .user {
  position: relative;
  padding: 0 0 0 50px;
  display: block;
  cursor: pointer;
  margin: 0 0 20px; }

.chat-users .user .avatar {
  top: 0;
  left: 0; }

.chat .avatar {
  width: 40px;
  height: 40px;
  position: absolute; }

.chat .avatar img {
  display: block;
  border-radius: 20px;
  height: 100%; }

.chat .avatar .status.off {
  border: 1px solid #5a5a5a;
  background: #ffffff; }

.chat .avatar .status.online {
  background: #4caf50; }

.chat .avatar .status.busy {
  background: #ffc107; }

.chat .avatar .status.offline {
  background: #ed4e6e; }

.chat-users .user .status {
  bottom: 0;
  left: 28px; }

.chat .avatar .status {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  position: absolute; }

.chat-users .user .name {
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.chat-users .user .status {
  font: 200 14px/20px "Oxygen", sans-serif;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

/* Chat body */
.chat-body .answer.left {
  padding: 0 0 0 58px;
  text-align: left;
  float: left; }

.chat-body .answer {
  position: relative;
  max-width: 600px;
  overflow: hidden;
  clear: both; }

.chat-body .answer.left .avatar {
  left: 0; }

.chat-body .answer .avatar {
  bottom: 36px; }

.chat .avatar {
  width: 40px;
  height: 40px;
  position: absolute; }

.chat .avatar img {
  display: block;
  border-radius: 20px;
  height: 100%; }

.chat-body .answer .name {
  font-size: 14px;
  line-height: 36px; }

.chat-body .answer.left .avatar .status {
  right: 4px; }

.chat-body .answer .avatar .status {
  bottom: 0; }

.chat-body .answer.left .text {
  background: #ebebeb;
  color: #333333;
  border-radius: 8px 8px 8px 0; }

.chat-body .answer .text {
  padding: 12px;
  font-size: 16px;
  line-height: 26px;
  position: relative; }

.chat-body .answer.left .text:before {
  left: -30px;
  border-right-color: #ebebeb;
  border-right-width: 12px; }

.chat-body .answer .text:before {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  border: 18px solid transparent;
  border-bottom-width: 0; }

.chat-body .answer.left .time {
  padding-left: 12px;
  color: #333333; }

.chat-body .answer .time {
  font-size: 16px;
  line-height: 36px;
  position: relative;
  padding-bottom: 1px; }

/* Right section */
.chat-body .answer.right {
  padding: 0 58px 0 0;
  text-align: right;
  float: right; }

.chat-body .answer.right .avatar {
  right: 0; }

.chat-body .answer.right .avatar .status {
  left: 4px; }

.chat-body .answer.right .text {
  background: #222C3C;
  color: #ffffff;
  border-radius: 8px 8px 0 8px; }

.chat-body .answer.right .text:before {
  right: -30px;
  border-left-color: #222C3C;
  border-left-width: 12px; }

.chat-body .answer.right .time {
  padding-right: 12px;
  color: #333333; }

/* Add Form */
.chat-input {
  clear: both;
  position: relative;
  margin: 0;
  padding: 20px;
  background: #1576c2; }

.chat-input input {
  border: none;
  background: none;
  display: block;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
  padding: 0;
  color: #ffffff; }

.chat input {
  -webkit-appearance: none;
  border-radius: 0; }

.chat-input .answer-btn-1 {
  background: url("../img/icons/icon-40.png") 50% 50% no-repeat;
  right: 56px; }

.chat-input .answer-btn {
  display: block;
  cursor: pointer;
  width: 36px;
  height: 36px;
  position: absolute;
  top: 50%;
  margin-top: -18px; }

.chat-input .answer-btn-2 {
  background: url("../img/icons/icon-41.png") 50% 50% no-repeat;
  right: 20px; }

.chat input::-webkit-input-placeholder {
  color: #fff; }

.chat input:-moz-placeholder {
  /* Firefox 18- */
  color: #fff; }

.chat input::-moz-placeholder {
  /* Firefox 19+ */
  color: #fff; }

.chat input:-ms-input-placeholder {
  color: #fff; }

.chat input {
  -webkit-appearance: none;
  border-radius: 0; }
